html,body {
  background-color: rgba(black,1); }

// side bar
.mainFrame {
  position: relative;
  min-height: 100vh;
  width: 100%;
  display: flex;
  overflow: hidden;
  .side-bar {
    width: 40px;
    background-color: black;
    z-index: 10;
    top: 0;
    @include phone {
      width: 20px;
      background-size: contain; }

    &.left {
      left: 0;
      @include bgi('side.png'); }
    &.right {
      right: 0;
      @include bgi('side2.png'); } }
  @include phone {
    .side-bar {
      &.right {
        display: none; } } } }



// main container
.mainContainer {
  width: 100%;
  position: relative;
  z-index: 20;
  .page:not(:last-child) {
    margin-bottom: 20px; } }


// index
#index {
  height: 100vh;
  position: relative;
  z-index: 10;
  @include phone {
    width: calc(100% + 20px);
    left: -20px; }
  .content {
    position: absolute;
    bottom: -20px;
    left: 10px;
    color: white;
    opacity: 0.85;
    z-index: 3;
    @include phone {
      bottom: -8px; }
    .subTitle {
      font-size: 36px;
      margin-bottom: 20px;
      letter-spacing: 0.2em;
      font-weight: 600;
      @include phone {
        font-size: 3.5vw; } }
    .title {
      font-size: 165px;
      letter-spacing: 0.1em;
      line-height: 1.1em;
      font-weight: 600;
      @include phone {
        font-size: 16vw; } } }
  .tags {
    @include full;
    .tag {
      position: absolute;
      &.tag1 {
        @include tag('tag01.png');
        // top: 50%
        // margin-top: -300px
        top: 70px;
        right: 0px; }
      &.tag2 {
        @include tag('tag02.png');
        top: 260px;
        right: 50px;
        @include phone {
          top: 160px;
          right: 10px; } }
      &.tag3 {
        @include tag('tag03.png');
        right: 50px;
        bottom: -130px;
        @include phone {
          bottom: 0;
          right: 10px; } }

      &.tag4 {
        @include tag('tag04.png');
        left: 55px;
        bottom: 440px;
        // margin-top: -180px
        @include phone {
          left: 10px;
          bottom: 270px; } }


      &.tag5 {
        @include tag('tag05.png');
        bottom: 230px;
        left: 48px;
        @include phone {
          left: 10px;
          bottom: 150px; } } } }
  .bg {
    @include full;
    @include coverBg('index01.png');
    @include phone {
      @include coverBg('index01_m.png'); }

    .bg-mask {
      @include full;
      background-color: rgba(black,.2); } } }


