#pic-section {
  background-color: #F3EEE9;
  position: relative;
  .container {
    max-width: 1160px;
    padding: 0 35px 50px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 3;
    @include phone {
      padding: 0 15px 50px;
      align-items: flex-start; }
    .txt {
      margin-top: -20px;
      @include phone {
        margin-top: 5px; } }
    .title {
      font-size: 100px;
      letter-spacing: 0.1em;
      line-height: 120%;
      font-weight: 500;
      @include phone {
        font-size: 13vw; } }
    .subtitle {
      font-weight: 500;
      font-size: 24px;
      letter-spacing: 0.1em;
      margin-top: 15px;
      @include phone {
        font-size: 4.5vw; } }
    .picWall {
      margin-top: 45px;
      width: 100%;

      .gutter-sizer {
        width: 1%;
        // @media screen and (max-width: 1024px)
        //   width: 33%
        @include phone {
          width: 2%; } }


      .grid-sizer,.grid-item {
        background-color: #fff;
        border-radius: 8px;
        overflow: hidden;
        width: 19%;
        margin-bottom: 15px;

        @media screen and (max-width: 1024px) {
          width: 24%; }
        @media screen and (max-width: 850px) {
          width: 32%; }
        @include phone {
          width: 48%; }


        img {
          display: block;
          width: 100%; }
        .media-content {
          padding: 10px 15px 15px;
          font-size: 14px;
          line-height: 150%;
          letter-spacing: 0.02em;
          .media-footer {
            display: flex;
            margin-top: 10px;
            color: #B3B3B3;
            font-size: 12px;
            line-height: 120%;
            letter-spacing: 0.1em;
            > * {
              margin-right: 15px; }
            .m-like {
              @include bg('i-heart.png');
              display: inline-block;
              margin-right: 5px;
              vertical-align: text-top;
              margin-top: 4px; }

            .m-comment {
              @include bg('i-comment.png');
              display: inline-block;
              margin-right: 5px;
              vertical-align: text-top;
              margin-top: 4px; } } } } }
    .grid-loading {
      font-size: 16px;
      color: #B3B3B3;
      margin: 20px;
      letter-spacing: 0.2em;
      text-align: center; } }

  .tags {
    .tag1 {
      @include tag('p-tag1.png');
      top: 0;
      right: 0; }
    .tag2 {
      @include tag('p-tag2.png');
      top: 180px;
      right: 40px; }
    .tag3 {
      @include tag('p-tag3.png');
      right: 40px;
      bottom: 50px; }
    .tag4 {
      @include tag('p-tag4.png');
      top: 157px;
      left: 45px; }
    .tag5 {
      @include tag('p-tag5.png');
      bottom: 182px;
      left: 45px; } } }
